import React from 'react';
import Header from './Header.js';
import Footer from './Footer.js';
import Search from './Search.js';
import Result from './Result.js';
import covid19api from '../api/covid19api.js';


class App extends React.Component{
    state = {result: [],
             name:""   
            };
    
    onSearchSubmit = async (term) => {
        const response = await covid19api.get("", {
            "params":{
                "region":`${term}`
        }
    }); 
        if(response){
            this.setState({name: term});
            this.setState({result: response.data.data.summary});
        } else {
            alert("Check Country Name");
        }
    }

    render(){
        return (
            <div>
                    <div className="header">
                        <Header />
                    </div> 
                        <div className="search">
                            <Search onSubmit ={this.onSearchSubmit}/>
                        </div>
                        <div className="Result">
                            <Result name={this.state.name} data={this.state.result} />
                        </div>
                    <div className="footer">
                        <Footer />
                    </div>
            </div>
            );
    }
    
};

export default App;