import React from 'react';

const Result = (props) => {
    return (
       <div className="container mt-5 mb-5 text-center">
            <h1 className="country-name">{props.name.toUpperCase()}</h1>

            <div className="d-flex container justify-content-center">
                <div className="col-8">
                    <ul class="list-group text-center h4">
                        <li class="list-group-item bg-dark text-white">Confirmed: {props.data.total_cases}</li>
                        <li class="list-group-item bg-success text-white">Recovered: {props.data.recovered}</li>
                        <li class="list-group-item bg-warning text-white">Active: {props.data.active_cases}</li>
                        <li class="list-group-item bg-danger text-white">Decieved: {props.data.deaths}</li>
                    </ul>
                </div>
            </div>

       </div>
    );
};

export default Result;