import React from 'react';

class Search extends React.Component{
    
    state = { term : ""} 

    onFormSubmit = (e) => {
        e.preventDefault();
        this.props.onSubmit(this.state.term);
    }

    render() {
        return (
            <div className="container-fluid search">
                <form onSubmit={this.onFormSubmit}>
                    <div class="bg-primary rounded-lg p-4 form-group row justify-content-center">
                        <label for="inputEmail3" class="col-sm-2 col-form-label">Enter Country</label>
                            <div class="col-sm-4">
                                <input type="text" class="form-control" value= {this.state.value} onChange={(e) => this.setState({term: e.target.value})} placeholder="Enter A Country" />
                            </div>
                            <button type="submit" class="btn btn-dark">Search</button>  
                    </div>
                </form>
            </div>
        );
    }
};

export default Search;


